@mixin inputCommonClass {
            border-radius: 5px;
            padding: 16px 12px;
            height: 58px;
            background-color: #FFFFFF;
            // border: 1px solid #E3E3E3;
            font-size: 14px;
            color: #565656;
            font-weight: 700;
}


.toktown-custom-input{
    .normal-input{
        .ant-input{
           @include inputCommonClass()
    }

       // .ant-input:focus, .ant-input-focused{
