.toktown-webisiteView{
    margin-bottom: 20px;
    .webisiteView-wrapper{
        border: 1px solid #ff0201;
        border-radius: 5px;
        width: 100%;
        overflow: hidden;
        background: #000;
        position: relative;

        .blur{
            position: absolute;
            width: 100%;
            height: 440px;
            background: black;
            opacity: 0.1;
        }

        img{
            height: 440px;
            width: 100%;
        }
    }
}