.toktown-custom-button{
   .ant-btn-primary{
    background-color: #ff0201;
    color: #fff;
    
    text-shadow: unset;
    -webkit-box-shadow: unset;
    box-shadow: unset;
    height: 58px;
    border-radius: 5px;
    border: 1px solid transparent;
    width: 100%;
    font-weight: 700;
        span{
            font-size: 20px;
        }
   } 

   .ant-btn-primary:hover, .ant-btn-primary:focus{
    background-color: #d90201;
    border: 1px solid transparent;
   }
    
}